<template>
  <div>
    <form>
      <div class="row">
        <div class="">Останнє значення</div>
        <input v-model.number="this.$store.state.count" type="number" placeholder="номер старий">
      </div>
      <div class="row">
        <div class="">Нове значення</div>
        <input v-model.number="number1" type="number">
      </div>
      <div class="row">Різниця {{result}}</div>
      <div class="row">Сумма {{resultsum}} грн</div>
      
      <div v-on:click="ccol">click</div>
      Моё имя <input v-model="name">
    </form>
  </div>
</template>

<script>
  export default {
    name: "Whater",
    data() {
      return {
        number1: 0,
        name: ''
      }
    },
    computed: {
      result: function() {
        if (this.number1 > 0) {
          return this.number1 - this.$store.state.count;
        }
      },
      resultsum: function() {
        if (this.number1 > 0) {
          return this.$store.state.price * (this.number1 - this.$store.state.count)
        }
        
      }
      
    },
    methods: {
      ccol: function() {
        localStorage.name = this.name;
        localStorage.number1 = this.number1;
      }
    },
    mounted() {
      if (localStorage.name) {
        this.name = localStorage.name;
      }
      if (localStorage.name) {
        this.number1 = localStorage.number1;
      }
    }
  
  }
</script>

